import { reactive, ref, toRefs } from "@vue/reactivity";
import { getnetShop, getmulu } from "../assets/api/home";
import { useRoute } from "vue-router";
export default {
  setup() {
    let index = ref(0);
    let route = useRoute();
    console.log(route);
    const value = ref(3);
    let obj = reactive({
      netshop: [],
      muluData: [],
      img: ""
    });

    //修改样式
    let toindex = ref(0);
    let change = item => {
      // console.log(item);
      toindex.value = item;
    };
    let getnetShopFun = () => {
      getnetShop(route.query.key).then(data => {
        obj.netshop = data.data.results.body;
        obj.img = data.data.results.body.details;
      });
      getmulu(route.query.key).then(res => {
        obj.muluData = res.data.results.body.resouceList;
        console.log(obj.muluData);
      });
    };
    getnetShopFun();
    return {
      ...toRefs(obj),
      index,
      change,
      toindex,
      value
    };
  }
};